<template>
  <div class="sub-header">
    <div class="sub-header-inner">
      <div class="category-wrap">
        <div class="category-box">
          <span class="depth1">
            <span class="txt">커뮤니티</span>
          </span>
          <ul class="depth2">
            <li>자유게시판</li>
            <li>비밀게시판</li>
            <li>비밀게시판</li>
            <li>시사.이슈</li>
            <li>정보게시판</li>
          </ul>
        </div>

        <!-- 이하 동일한 형태로 나머지 코드 작성 -->
        
        <div class="category-banner-wrap">
          <img src="@/assets/images/category-banner.png" class="category-banner" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SubHeader',
  data() {
    return {
      isExistToken: false
    }
  },
  methods: {
    getCategories() {
      const token = localStorage.getItem('token');
      var link = 'http://' + window.location.host;
      var headers = {
        'Authorization': `Bearer ${token}`, // "Authorization" 헤더에 토큰 추가
        'Content-Type': 'application/json',
      };
      axios
        .get(link + '/api/common/getCategories', { headers: headers })
        .then(response => {
          this.categories = response.data;
          console.log(this.categories);
        })
        .catch(err => {
          console.log(err);
        })
    },
    created() {
      this.getCategories();
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/subheader.scss";
</style>